import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { useHistory, useParams } from 'react-router'
import { Breadcrumb, BreadcrumbItem, Table } from 'reactstrap'
import MyTable from '../../../components/UI/MyTable/MyTable'
import Spinner from '../../../components/Spinner/Spinner'
import { deletePartnersThunk, getPartnerDetailsThunk, setPartnerDetails } from '../../../redux/actions/partnerActions'

import styles from './PartnerDetails.module.scss'
import { getShopsThunk } from '../../../redux/actions/shopActions'
import DetailsTableRow from '../../../components/UI/MyTable/DetailsTableRow/DetailsTableRow'
import MyButton from '../../../components/UI/MyButton/MyButton'
import { NavLink } from 'react-router-dom'
import SuccessAlert from '../../../components/MyAlert/SuccessAlert/SuccessAlert'
import FailureAlert from '../../../components/MyAlert/FailureAlert/FailureAlert'
import { useAlert } from '../../../hooks/useAlert'
import FlexBlock from '../../../components/Blocks/FlexBlock/FlexBlock'
import { getTrimmedValues } from '../../../utils/utils'
import { useFormAndValidation } from '../../../hooks/useFormValiation'
import MyTableForm from '../../../components/UI/MyTable/MyTableForm/MyTableForm'
import TableInput from '../../../components/UI/MyTable/TableInput/TableInput'
import ClickableTH from '../../../components/UI/MyTable/ClickableTH/ClickableTH'
import TableNotFound from '../../../components/UI/MyTable/TableNotFound/TableNotFound'



const PartnerDetails = ({
    className, details, getPartnerDetailsThunk, setPartnerDetails, deletePartnersThunk,
    getShopsThunk, shops,
     ...props}) => {

  const {id} = useParams()
  const history = useHistory()

  const {values, handleChange, errors, isValid, } = useFormAndValidation()


  const { successAlertVisible, setSuccessAlertVisible, successMessage,
    failureAlertVisible, setFailureAlertVisible, failureMessage, checkRes} = useAlert()

  const [order, setOrder] = useState(true)
  const changeOrder = (field) => {
    setOrder(!order)

    const filterData = getTrimmedValues(values)

    const sortedData = {
        ...filterData,
        "sort_by": [
            {
              field,
              order: order? 'asc' : 'desc'
            }
        ]
    }
    
    getShopsThunk({
        ...sortedData,
        partner_uuid: id
    })
    .then(checkRes(false))
    .catch((err) => console.log(err))
}

  const deletePartner = () => {
    const confirmed = window.confirm("   ?");
    if (confirmed) {
        deletePartnersThunk([id])
        .then((res) => {
            checkRes()(res)
            return res
        })
        .then(res => {
            if (res.ok) {
                setTimeout(() => {
                    history.goBack()
                }, 3000)
            }
        })
        .catch((err) => console.log(err))
    }
   
  }

  const onSubmit = (evt) => {
    evt.preventDefault()

    const filterData = getTrimmedValues(values)

    if (filterData.limit) {
        filterData.limit = Number(filterData.limit)
    }

    getShopsThunk({
        ...filterData,
        partner_uuid: id
    })
    .then(checkRes(false))
    .catch((err) => console.log(err))

    console.log(filterData);
  }

  useEffect(() => {
    getPartnerDetailsThunk([id])
    .then(checkRes(false))
    .catch((err) => console.log(err))
    
    return () => {
        setPartnerDetails({
            uuid: '',
            inn: '',
            name: ''
        })
    }
    // eslint-disable-next-line 
  }, [])

  useEffect(() => {
    getShopsThunk({
        partner_uuid: id
    })
    .then(checkRes(false))
    .catch((err) => console.log(err))
    
    return () => {
      
    }
    // eslint-disable-next-line 
  }, [])

    const cls = [
        styles.PartnerDetails,
        className
    ]
    if (!details) {
        return <h1>  </h1>
    }

    if (!details.uuid) {
        return <Spinner/>
    }

    return (
        <div className={cls.join(' ')}>
        <Breadcrumb tag="nav" listTag="div">
          <BreadcrumbItem tag={NavLink} to='/partners' exact> </BreadcrumbItem>
          <BreadcrumbItem active tag="span">  &nbsp; <strong>{details.name}</strong></BreadcrumbItem>
        </Breadcrumb>
            <center><h1>  </h1></center>
            <MyTable>
                <DetailsTableRow
                    label='uuid'
                    data={details.uuid}
                />
                <DetailsTableRow
                    label=''
                    data={details.inn}
                />
                <DetailsTableRow
                    label=''
                    data={details.name}
                />
             
            </MyTable>

            <FlexBlock>
                <MyButton
                    color={'secondary'}
                    onClick={() => history.push(`/partners/update/${id}`)}
                    buttonText=''
                />
                <MyButton
                    color={'danger'}
                    onClick={deletePartner}
                    buttonText=' '
                />
            </FlexBlock>

            
            {/* <MyButton
                color={'success'}
                onClick={() => history.push(`/partners-acquirers/${id}`)}
                buttonText=' '
            /> */}
         

            <center><h2> </h2></center>
            <MyTableForm
                onSubmit={onSubmit}
                isValid={isValid}
                buttonText=''
            >
              
                <TableInput
                    name='name'
                    label=''
                    onChange={handleChange}
                    value={values.name}
                    errorText={errors.name}
                    minLength={1}
                    maxLength={199}
                />
                <TableInput
                    name='uuid'
                    label='uuid'
                    onChange={handleChange}
                    value={values.uuid}
                    errorText={errors.uuid}
                    minLength={1}
                    maxLength={199}
                />
                <TableInput
                        type='number'
                        name='limit'
                        label='- '
                        onChange={handleChange}
                        value={values.limit}
                        errorText={errors.limit}
                        style={{
                            width: `140px`
                        }}
                        min={1}
                        max={100}
                    />
            </MyTableForm>
            {
                shops && (

                    <Table hover>

                        <thead>
                            <tr>
                                <th></th>
                                <ClickableTH
                                    onClick={() => changeOrder('name')}
                                    label=''
                                />
                                <th>uuid</th>
                            </tr>
                        </thead>
                        <tbody>
                            { shops.length?
                                shops.map((shop, i) => {
                                    return (
                                        <tr 
                                            key={shop.uuid} 
                                            onClick={() => history.push(`/shops/${shop.uuid}`)}
                                        >
                                            <td>{i+1}</td>
                                            <td>{shop.name}</td>
                                            <td>{shop.uuid}</td>
                                        </tr>
                                    )
                                }) :
                                (
                                    <TableNotFound/>
                                )
                            }

                        </tbody>

                    </Table>

                )
            }

           
            <MyButton
                color={'primary'}
                onClick={() => history.push(`/add-shop/${id}`)}
                buttonText=' '
            />

            <SuccessAlert
                visible={successAlertVisible}
                setVisible={setSuccessAlertVisible}
                message={successMessage}
            />

                <FailureAlert
                   visible={failureAlertVisible}
                   setVisible={setFailureAlertVisible}
                   message={failureMessage}
                />




        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        details: state.partners.details,
        shops: state.shops.shops,
    }
}



export default connect(
    mapStateToProps, {
        getPartnerDetailsThunk, setPartnerDetails, deletePartnersThunk,
        getShopsThunk,
    }
    )(PartnerDetails)