// import myAxios from '../../configs/modified-axios'
import {accountAxios as myAxios} from '../../configs/modified-axios'

// import { mockAccounts } from '../../utils/mock-accounts'
import { getAccessToken } from '../../utils/utils'
import { ACCOUNTS_LOADING, GET_ACCOUNTS_DETAILS, GET_ACCOUNTS_FAILURE, GET_ACCOUNTS_SUCCESS } from './actionTypes'


export const getAccountsThunk = () => {
  return async (dispatch) => {
    dispatch(setAccountsLoading(true))

    try {
      // dispatch(setAccounts(mockAccounts)) // TEST
      const data = {
        access_token: getAccessToken(),
      }
      const res = await myAxios.post(`/api/get_accounts`, data)
      console.log(res);
      if (res.status === 200 && res.data.result) {
        dispatch(setAccountsFailure({ accounts: null }))
        dispatch(setAccounts(res.data.result.accounts))
      } else {
        dispatch(setAccountsFailure({ accounts: res.data.errors }))
      } 
    } catch (error) {
      dispatch(setAccountsFailure({ accounts: [' '] }))
    } finally {
      dispatch(setAccountsLoading(false))
    }
  }
}

export const getAccountDetailsThunk = (id) => {
  return async (dispatch) => {
    dispatch(setAccountsLoading(true))

    try {
      // dispatch(setAccountDetails(mockAccounts.find((account) => account.id === +id))) // TEST

      const data = {
        access_token: getAccessToken(),
        ids: [id]
      }
      const res = await myAxios.post(`/api/get_accounts_by_ids`, data)
      console.log(res);

      if (res.status === 200 && res.data.result) {
          dispatch(setAccountsFailure({ details: null }))
        dispatch(setAccountDetails(res.data.result.accounts[0]))
      } else {
        dispatch(setAccountsFailure({ details: res.data.errors }))
      }
    } catch (error) {
      dispatch(setAccountsFailure({ details: [' '] }))
      console.log(error);
    } finally {
      dispatch(setAccountsLoading(false))
    }
  }
}

export const setAccountsLoading = (status) => {
  return {
    type: ACCOUNTS_LOADING,
    status
  }
}

export const setAccountsFailure = (errors) => {
  return {
    type: GET_ACCOUNTS_FAILURE,
    errors
  }
}

export const setAccounts = (accounts) => {
  return {
    type: GET_ACCOUNTS_SUCCESS,
    accounts
  }
}

export const setAccountDetails = (details) => {
  return {
    type: GET_ACCOUNTS_DETAILS,
    details
  }
}